import proj4 from "proj4";

export const COORDINATE_TYPES = {
  WGS84: "EPSG:4326",
  TWD97: "EPSG:3826",
  TWD67: "EPSG:3828"
};

export enum PROJ_TYPES {
  PROJ_TYPE_WGS84,
  PROJ_TYPE_TWD97,
  PROJ_TYPE_TWD67
}

// defs from https://epsg.io/
proj4.defs([
  [
    // WGS 84
    COORDINATE_TYPES.WGS84,
    "+proj=longlat +datum=WGS84 +no_defs"
  ],
  [
    // TWD97 / TM 2 zone 121
    COORDINATE_TYPES.TWD97,
    "+proj=tmerc +lat_0=0 +lon_0=121 +k=0.9999 +x_0=250000 +y_0=0 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs"
  ],
  [
    // TWD67 / TM 2 zone 121
    COORDINATE_TYPES.TWD67,
    "+proj=tmerc +towgs84=-752,-358,-179,-0.0000011698,0.0000018398,0.0000009822,0.00002329 +lat_0=0 +lon_0=121 +x_0=250000 +y_0=0 +k=0.9999 +ellps=aust_SA +units=m +no_defs"
  ]
]);

export type CoordinatesType = [number | null, number | null];

function convert(
  fromProjType: string,
  toProjType: string,
  coordinates: CoordinatesType
) {
  if (coordinates[0] && coordinates[1]) {
    const formated = [coordinates[0], coordinates[1]];
    return proj4(fromProjType.toString(), toProjType.toString(), formated);
  }

  return [null, null];
}

export default convert;
export function convertFromWGS84ToTWD97(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.WGS84, COORDINATE_TYPES.TWD97, coordinates);
}

export function convertFromWGS84ToTWD67(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.WGS84, COORDINATE_TYPES.TWD67, coordinates);
}

export function convertFromTWD97ToWGS84(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.TWD97, COORDINATE_TYPES.WGS84, coordinates);
}

export function convertFromTWD97ToTWD67(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.TWD97, COORDINATE_TYPES.TWD67, coordinates);
}

export function convertFromTWD67ToWGS84(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.TWD67, COORDINATE_TYPES.WGS84, coordinates);
}

export function convertFromTWD67ToTWD97(coordinates: CoordinatesType) {
  return convert(COORDINATE_TYPES.TWD67, COORDINATE_TYPES.TWD97, coordinates);
}
