import { useState, useCallback } from "react";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Box from "@material-ui/core/Box";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import EditIcon from "@material-ui/icons/Edit";

import {
  convertFromWGS84ToTWD97,
  convertFromWGS84ToTWD67,
  convertFromTWD97ToWGS84,
  convertFromTWD97ToTWD67,
  convertFromTWD67ToWGS84,
  convertFromTWD67ToTWD97
} from "utils/coordinate";

import {
  TITLE_WGS84,
  WGS84_LONGITUDE,
  WGS84_LATITUDE,
  TITLE_TWD97,
  TWD97_X,
  TWD97_Y,
  TITLE_TWD67,
  TWD67_X,
  TWD67_Y
} from "utils/constants";

const EDIT_INDEX_WGS84 = 1;
const EDIT_INDEX_TWD97 = 2;
const EDIT_INDEX_TWD67 = 3;

type CONVERT_FUNCS_OPTIONS = {
  [key: number]: Function;
};

type ALL_CONVERT_FUNCS_OPTIONS = {
  [key: number]: CONVERT_FUNCS_OPTIONS;
};

const ALL_CONVERT_FUNCS: ALL_CONVERT_FUNCS_OPTIONS = {
  [EDIT_INDEX_WGS84]: {
    [EDIT_INDEX_TWD97]: convertFromWGS84ToTWD97,
    [EDIT_INDEX_TWD67]: convertFromWGS84ToTWD67
  },
  [EDIT_INDEX_TWD97]: {
    [EDIT_INDEX_WGS84]: convertFromTWD97ToWGS84,
    [EDIT_INDEX_TWD67]: convertFromTWD97ToTWD67
  },
  [EDIT_INDEX_TWD67]: {
    [EDIT_INDEX_WGS84]: convertFromTWD67ToWGS84,
    [EDIT_INDEX_TWD97]: convertFromTWD67ToTWD97
  }
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& .MuiTextField-root": {
        margin: theme.spacing(1),
        width: `calc(50% - ${theme.spacing(1) * 2}px)`
      }
    },
    textLabel: {
      padding: theme.spacing(1)
    },
    textFieldGroup: {
      marginBottom: theme.spacing(4)
    },
    containerCenter: {
      display: "flex",
      alignItems: "center"
    }
  })
);

export default () => {
  const classes = useStyles();
  const [editIndex, setEditIndex] = useState<number>(EDIT_INDEX_WGS84);
  const [xValue, setXValue] = useState<null | number>(null);
  const [yValue, setYValue] = useState<null | number>(null);

  const changeXValue = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const value = parseFloat(event.target.value);
      setXValue(isNaN(value) ? null : value);
    },
    []
  );
  const changeYValue = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const value = parseFloat(event.target.value);
      setYValue(isNaN(value) ? null : value);
    },
    []
  );

  const convertFuncs = ALL_CONVERT_FUNCS[editIndex];

  const editWGS84 = useCallback(() => {
    setXValue(null);
    setYValue(null);
    setEditIndex(EDIT_INDEX_WGS84);
  }, []);
  const isShowEditWGS84 = editIndex !== EDIT_INDEX_WGS84;
  const [wgs84Longitude, wgs84Latitude] = convertFuncs[EDIT_INDEX_WGS84]
    ? convertFuncs[EDIT_INDEX_WGS84]([xValue, yValue])
    : [xValue, yValue];

  const editTWD97 = useCallback(() => {
    setXValue(null);
    setYValue(null);
    setEditIndex(EDIT_INDEX_TWD97);
  }, []);
  const isShowEditTWD97 = editIndex !== EDIT_INDEX_TWD97;
  const [twd97X, twd97Y] = convertFuncs[EDIT_INDEX_TWD97]
    ? convertFuncs[EDIT_INDEX_TWD97]([xValue, yValue])
    : [xValue, yValue];

  const editTWD67 = useCallback(() => {
    setXValue(null);
    setYValue(null);
    setEditIndex(EDIT_INDEX_TWD67);
  }, []);
  const isShowEditTWD67 = editIndex !== EDIT_INDEX_TWD67;
  const [twd67X, twd67Y] = convertFuncs[EDIT_INDEX_TWD67]
    ? convertFuncs[EDIT_INDEX_TWD67]([xValue, yValue])
    : [xValue, yValue];

  return (
    <Box className={classes.root} m={1}>
      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_WGS84}
          </Typography>
          {isShowEditWGS84 && (
            <IconButton aria-label="edit" onClick={editWGS84}>
              <EditIcon />
            </IconButton>
          )}
        </div>
        <TextField
          label={WGS84_LONGITUDE}
          type="number"
          variant={!isShowEditWGS84 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditWGS84
          }}
          value={wgs84Longitude != null ? wgs84Longitude : ""}
          onChange={changeXValue}
        />
        <TextField
          label={WGS84_LATITUDE}
          type="number"
          variant={!isShowEditWGS84 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditWGS84
          }}
          value={wgs84Latitude != null ? wgs84Latitude : ""}
          onChange={changeYValue}
        />
      </div>

      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_TWD97}
          </Typography>
          {isShowEditTWD97 && (
            <IconButton aria-label="edit" onClick={editTWD97}>
              <EditIcon />
            </IconButton>
          )}
        </div>
        <TextField
          label={TWD97_X}
          type="number"
          variant={!isShowEditTWD97 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditTWD97
          }}
          value={twd97X != null ? twd97X : ""}
          onChange={changeXValue}
        />
        <TextField
          label={TWD97_Y}
          type="number"
          variant={!isShowEditTWD97 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditTWD97
          }}
          value={twd97Y != null ? twd97Y : ""}
          onChange={changeYValue}
        />
      </div>
      <div className={classes.textFieldGroup}>
        <div className={classes.containerCenter}>
          <Typography variant="h5" component="h3" className={classes.textLabel}>
            {TITLE_TWD67}
          </Typography>
          {isShowEditTWD67 && (
            <IconButton aria-label="edit" onClick={editTWD67}>
              <EditIcon />
            </IconButton>
          )}
        </div>
        <TextField
          label={TWD67_X}
          type="number"
          variant={!isShowEditTWD67 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditTWD67
          }}
          value={twd67X != null ? twd67X : ""}
          onChange={changeXValue}
        />
        <TextField
          label={TWD67_Y}
          type="number"
          variant={!isShowEditTWD67 ? "outlined" : "standard"}
          InputProps={{
            readOnly: isShowEditTWD67
          }}
          value={twd67Y != null ? twd67Y : ""}
          onChange={changeYValue}
        />
      </div>
    </Box>
  );
};
